/* ----------------------------------------- */
  /* Posicionamento de divs */

.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 0;
  height: 100%;
  /* min-height: 100%; */
  width: 100%;
  /* flex-wrap: wrap; */
  background-color: black;
}

.content-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  /* position: fixed; */
  margin:0;
  height: 100%;
  width: 100%;
  /* overflow:hidden; */
  /* height: 100vh; */
  /* background-color: black; */
  
}
.link-voltar-container {
  width: 100%;
