/* ----------------------------------------- */
    /* Headers e footers */
.app-header {
  display: flex;
  flex-direction: row;
  position: sticky;
  top: 0;
  width: 100%;
}

.app-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: sticky;
  bottom: 0;
  width: 100%;
}