.MV-header {
  margin: 0;
  width: 100%;
  position: sticky;
  top: 0;
  padding-top: 10px;
  padding-bottom: 20px;
}

.MV-carousel-container {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.MV-content-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0;
}

.MV-header-box-1 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.MV-header-box-2 {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.main-carousel {
  width: 100%;
}

.currency-card {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 144px;
  height: 184px;
  border: solid 2px #53565E;
  border-radius: 15px;
  padding: 5px 20px;
  margin-right: 20px;
}

.card-text {
  margin-bottom: 7px;
}

.card-image {
  margin-bottom: 10px;
}

.cards-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 40%;
}