:root {
  --primary: #16d28e;
  --secondary: #ff914d;
  --complementary: #4ab8df;
  --grey: #727272;
  --dark-grey: #272727;
  --light-grey: #f6f4f4;
  --green-light: #bbf1b2;
}

hr {
  width: 100%;
  padding: 0;
  border-top: 1px solid var(--green-light);
  opacity: 0.33;
}

.btn-help {
  padding: 0;
  border: 0;
  width: 48px;
  height: 48px;
  color: var(--primary);
  background-color: transparent;
}

footer, header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
}

.screen {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-height: 100vh;
  padding: 16px 15px;
}

.main-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  max-height: 100vh;
  padding: 0;
}

.home {
  background-image: url('./imagesIcons/cataventos.jpg');
  background-size: auto 100%;
  background-position: 50% center;
}

.home .title {
  text-align: left;
  font-size: 18px;
  width: 70%;
  padding-bottom: 24px;
}

.s-btn {
  border-radius: 30px;
  border: none;
  font-size: 1rem;
  font-weight: 700;
  margin: 10px 5px;
  padding: 12px 25px;
  width: 95%;
}

.s-input {
  font-size: 1rem !important;
  border-radius: 30px !important;
  padding-left: 16px !important;
  border: none !important;
  max-width: 90% !important;
}

.s-container, .text-container {
  text-align: center;
}

.text-container {
  padding: 16px 0;
  color: var(--light-grey);
}

.s-white {
  background-color: var(--light-grey) !important;
  color: var(--primary) !important;
}

.s-green {
  background-color: var(--primary);
  color: var(--light-grey);
}

.s-grey {
  background-color: var(--dark-grey) !important;
  color: var(--light-grey) !important; 
}

.s-grey::placeholder {
  color: var(--grey) !important; 
}

img {
  height: 100px;
  width: 100px;
}

.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin: 10px 5px;
  height: 100%;
  min-height: 100%;
}

header {
  position: sticky;
}

footer {
  position: sticky;
}

.big-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  top:0px;
  left:0px;
  width:100%;
  height: 100vh;
  background-color: black;
}

p {
  color: var(--light-grey);
  text-align: center;
  font-size: 0.85rem;
  padding: 3px 0;
}

#root {
  margin: 0;
  padding: 0;
}
